:root {
  --min-font-size: 10px;
  --control-size: clamp(20px, 6vw, 40px);
  --problem-size: min(16vw, 16vh);
}

@media screen and (max-width: 600px) {
  :root {
    --min-font-size: 8px;
    --control-size: clamp(20px, min(6vh, 12vw), 40px);
  }
}

* {
  font-family: Arial, Helvetica, sans-serif;
}

html, body {
  font-size: var(--min-font-size);
  height: 100%;
  margin: 0;
  padding: 0;
}

input, select, label {
  font-size: var(--min-font-size);
}

body {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.spacer {
  min-height: 3px;
  flex-grow: 1;
}

.hidden {
  display: none !important;
}

#temp-message {
  z-index: 20;
  width: -moz-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  vertical-align: middle;
  opacity: 0;
  background-color: #fff;
  border: thin solid #000;
  border-radius: 1em;
  padding: .2em 1em;
  font-size: clamp(12px, 4vw, 24px);
  position: absolute;
  top: max(4px, .2vh);
}

.show-message {
  animation: show-briefly 4s ease-in-out;
}

@keyframes show-briefly {
  0% {
    opacity: 0;
  }

  20% {
    opacity: 1;
  }

  60% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

#problem {
  font-size: var(--problem-size);
  width: 2.5em;
  border: thin solid #888;
  grid-template-rows: 1fr 1fr .3fr 1fr;
  grid-template-columns: 1fr 3fr;
  margin: auto;
  padding: .25em;
  display: grid;
}

#upper {
  text-align: right;
  grid-area: 1 / 2;
}

#op {
  cursor: pointer;
  grid-area: 2 / 1;
}

#lower {
  text-align: right;
  grid-area: 2 / 2;
}

#line {
  height: 1px;
  border-top: .08em solid #000;
  grid-area: 3 / 1 / auto / -1;
}

#answer-box {
  grid-area: 4 / 1 / auto / -1;
}

#answer {
  font-size: var(--problem-size);
  text-align: right;
  box-sizing: border-box;
  width: 100%;
}

#correct-check {
  width: 1em;
  height: 1em;
  fill: green;
  backface-visibility: hidden;
  grid-area: 1 / 1;
  transform: scale(0);
}

#correct-check.correct {
  animation: correct .5s ease-in-out;
  transform: scale(1);
}

#correct-check.correct-instant {
  transform: scale(1);
}

@keyframes correct {
  0% {
    transform: scale(0);
  }

  75% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

#control-bar {
  width: calc(100% - 4px);
  border-bottom: thin solid #888;
  flex-direction: row;
  flex-grow: 0;
  padding: 0 .1em;
  display: flex;
}

.center {
  text-align: center;
  vertical-align: middle;
  font-size: var(--control-size);
  flex-grow: 1;
}

.icon {
  margin: .2em;
  display: inline-block;
}

.icon svg {
  width: var(--control-size);
  height: var(--control-size);
  cursor: pointer;
  display: inline-block;
}

#progress-outer {
  min-height: var(--control-size);
  width: 100vw;
  background-repeat: no-repeat;
  background-size: 100%;
  flex-grow: 0;
}

#progress {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 0%;
}

.bronze {
  background-image: linear-gradient(to right, #ca7345, #732100);
}

.silver {
  background-image: linear-gradient(to right, #d7dde8, #757f9a);
}

.gold {
  background-image: linear-gradient(to right, #eecd3f, #99771f);
}

.overlay {
  z-index: 10;
  background-color: #606060e6;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.close-button {
  z-index: 11;
  width: 40px;
  height: 40px;
  cursor: pointer;
  position: absolute;
  top: -50px;
  right: -5px;
}

.contents {
  width: -moz-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #fff;
  border: thin solid #000;
  border-radius: 1em;
  flex-direction: column;
  justify-content: center;
  padding: 1em;
  font-size: clamp(10px, 2vw, 20px);
  display: flex;
  position: relative;
}

.contents table {
  font-size: clamp(var(--min-font-size), min(2vw, 2vh), 20px);
  border-collapse: collapse;
}

.contents table th, td {
  width: 2.5em;
  height: 2.5em;
  text-align: center;
  border: thin solid #888;
}

.contents table.subtraction {
  font-size: clamp(var(--min-font-size), min(1.6vw, 1.6vh), 20px);
}

.contents table.subtraction th, td {
  width: 2em;
  height: 2em;
}

.contents table.stats th:first-child {
  width: 1em;
  height: 1em;
  cursor: pointer;
  font-size: 200%;
}

.contents table.stats th, td:first-child {
  color: #888;
}

.great {
  background-image: linear-gradient(to right, #74c476, #41ab5d);
}

.good {
  background-image: linear-gradient(to right, #f7fcb9, #addd8e);
}

.ok {
  background-image: linear-gradient(to right, #fdd0a2, #fdae6b);
}

.bad {
  background-image: linear-gradient(to right, #fc9272, #fb6a4a);
}

.fill-bronze {
  fill: #ca7345;
}

.fill-silver {
  fill: #d7dde8;
}

.fill-gold {
  fill: #eecd3f;
}

.calendar th, .calendar td {
  font-size: clamp(var(--min-font-size), min(3vw, 3vh), 30px);
}

.calendar th {
  color: #888;
  font-weight: normal;
}

.calendar svg {
  width: 3em;
  height: 3em;
}

.calendar td {
  text-align: left;
  vertical-align: top;
  width: 3em;
  height: 4em;
  padding: .2em;
}

.calendar .date {
  color: #888;
  font-size: 60%;
}

.help, .share {
  max-width: min(80vw, 40em);
  white-space: pre-line;
}

.help-links {
  padding-top: 1em;
}

.attribution {
  border: thin solid silver;
  border-radius: 6px;
  margin: 1.5em 0;
  padding: 1em;
  font-size: 80%;
}

#share-text {
  font-size: clamp(10px, 4vw, 30px);
}

.share-links {
  text-align: right;
  white-space: initial;
  margin-top: 1em;
}

.settings label, .settings input, .settings select, .settings td {
  font-size: clamp(12px, 3.5vw, 20px);
}

.settings label {
  text-align: right;
}

.settings input {
  width: 3em;
}

.settings td {
  text-align: left;
  height: 1.75em;
  border: none;
}

.settings tr td:first-child {
  text-align: right;
  width: -moz-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding-right: .75em;
}

